interface IconProps {
  size?: number
  tipText?: string
  tipLocation?: string
  onboarding?: any
  withCount?: any
  onClick?: any
  glyph?: string
}
class Icon extends React.Component<IconProps> {
  static defaultProps = {
    size: 32,
  };
  
  render() {
    const {
      size,
      tipText,
      tipLocation,
      onboarding,
      withCount,
      onClick,
      glyph,
    } = this.props;

    return (
      <SvgWrapper
        size={size}
        tipText={tipText}
        tipLocation={tipLocation}
        onboarding={onboarding}
        count={withCount}
        className={'icon'}
        onClick={onClick}
      >
        <InlineSvg
          fillRule="evenodd"
          clipRule="evenodd"
          strokeLinejoin="round"
          strokeMiterlimit="1.414"
          xmlns="http://www.w3.org/2000/svg"
          aria-labelledby="title"
          viewBox="0 0 32 32"
          preserveAspectRatio="xMidYMid meet"
          fit
          id={glyph}
        >
          <title id="title">{glyph}</title>
          <Glyph glyph={glyph} />
        </InlineSvg>
      </SvgWrapper>
    );
  }
}

export default Icon;
